body {
  background-color: rgb(19, 19, 19);
}

.container {
  max-width: 1440px;
  height: 1198px;
  background-color: #FAFBFF;
  margin: auto;
  display: flex;
  flex-direction: row;
}

.nav_container {
  width: 306px;
  height: 100%;
  box-shadow: 0px 10px 60px 0px rgba(226, 236, 249, 0.5);
  background: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.nav_container-logo {
  padding: 41px 0 0 28px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.nav_container-logo img {
  width: 37px;
  height: 37px;
}

.nav_container-logo p {
  margin: 0 0 0 8px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 26px;
}

.nav_container-logo p span {
  margin-left: 5px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 10px;
}

.nav_list {
  margin-top: 66px;
  margin-left: 28px;
  margin-right: 36px;
}

.nav_list-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 29px;
  padding: 11px 8px 11px 11px;
  align-items: center;
  color: #9197B3;
}

.nav_list-item.active {
  background-color: #5932EA;
  border-radius: 8px;
  color: #ffffff;
}

.nav_list-item.active p {
  color: #ffffff;
}

.nav_list-item div {
  display: flex;
  flex-direction: row;
  margin-left: 13px;
}

.nav_list-item p {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 14px;
  color: #9197B3;
  margin: 0 0 0 16px;
}

.user {
  margin-left: 28px;
  margin-bottom: 76px;
  display: flex;
  flex-direction: row;
}

.user-data {
  margin-left: 12px;
}

.user-data p {
  margin: 0;
}

.user-data-name {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 14px;
  color: #000000;
  line-height: 21px;
  letter-spacing: 1%;
}

.user-data-position {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 12px;
  color: #757575;
  line-height: 18px;
  letter-spacing: 1%;
}

.main_container {
  width: 1134px;
  height: 1198px;
}

.main_container-else {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 40px;
  text-transform: uppercase;
  width: 100%;
  vertical-align: middle;
  padding-top: 40%;
  display: none;
}

.main_container-else p {
text-align: center;
vertical-align: middle;
}

.main_container-customers {
  display: block;
}

.main_containers-hello p {
  color: #000000;
font-family: "Poppins", sans-serif;
font-size: 24px;
font-weight: 500;
line-height: 36px;
letter-spacing: 0%;
text-align: left;
padding: 41px 0 0 71px;
margin: 0;
}

.main_container-customers-section {
  margin: 51px 0 0 71px;
  border-radius: 30px;
  box-shadow: 0px 10px 60px 0px rgba(226, 236, 249, 0.5);
  background: #ffffff;
  position: absolute;
  width: 968px;
  height: 812px;
}

.main_container-customers-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.main_container-customers-title h2 {
  color: #000000;
font-family: "Poppins", sans-serif;
font-size: 22px;
font-weight: 600;
line-height: 33px;
letter-spacing: -1%;
text-align: left;
margin: 30px 0 0 38px;
}

.main_container-customers-title p {
  color: rgb(22, 192, 152);
font-family: "Poppins", sans-serif;
font-size: 14px;
font-weight: 400;
line-height: 21px;
letter-spacing: -1%;
text-align: left;
margin: 7px 0 40px 38px;
}

.main_container-customers-search {
width: 216px;
height: 38px;
margin-right: 53px;
border-radius: 10px;
background: rgb(249, 251, 255);
border: transparent;
padding-left: 11px;
display: flex;
align-items: center;
}


.main_container-customers-search input {
  background: rgb(249, 251, 255);
  border: transparent;
  margin-left: 8px;
}

.main_container-customers-search input:focus {
  outline: none;
}

.main_container-customers-search input::placeholder {
  color: #B5B7C0;
}

.search-icon {
  width: 24px;
  height: 24px;
}




